"use client";
import { useState } from 'react';
import { calculateGasLoad, recommendPipe } from './utils/gasLogic';

export default function Home() {
  const [beds, setBeds] = useState(0);
  const [type, setType] = useState('ward');
  const [result, setResult] = useState(null);

  const handleCalculate = () => {
    // This calls the 'Brain' file you saved yesterday
    const data = calculateGasLoad(type, Number(beds));
    const pipe = recommendPipe(data.flow);
    
    // This saves the result to show on screen
    setResult({ ...data, pipe });
  };

  return (
    <div className="min-h-screen bg-slate-50 flex flex-col items-center p-10 font-sans">
      <h1 className="text-4xl font-extrabold text-blue-900 mb-2 tracking-tight">UbiFlow AI</h1>
      <p className="text-slate-500 mb-8 text-lg">HTM 02-01 Compliance Engine (MVP)</p>

      <div className="bg-white p-8 rounded-2xl shadow-xl w-full max-w-md border border-slate-100">
        
        {/* INPUT 1: Room Type */}
        <label className="block mb-2 font-semibold text-slate-700">Clinical Area</label>
        <select 
          className="w-full p-3 border border-slate-300 rounded-lg mb-5 focus:ring-2 focus:ring-blue-500 focus:outline-none" 
          value={type} 
          onChange={(e) => setType(e.target.value)}
        >
          <option value="ward">General Ward</option>
          <option value="icu_standard">ICU (Standard)</option>
          <option value="icu_high_flow">ICU (Ventilated/Covid)</option>
          <option value="theatre">Operating Theatre</option>
        </select>

        {/* INPUT 2: Bed Count */}
        <label className="block mb-2 font-semibold text-slate-700">Number of Beds/Bays</label>
        <input 
          type="number" 
          className="w-full p-3 border border-slate-300 rounded-lg mb-6 focus:ring-2 focus:ring-blue-500 focus:outline-none"
          value={beds}
          onChange={(e) => setBeds(e.target.value)}
        />

        {/* BUTTON */}
        <button 
          onClick={handleCalculate}
          className="w-full bg-blue-600 text-white font-bold p-4 rounded-lg hover:bg-blue-700 transition-colors shadow-md"
        >
          Verify Compliance
        </button>

        {/* OUTPUT RESULTS (Only shows after you click) */}
        {result && (
          <div className="mt-8 p-6 bg-blue-50 border border-blue-100 rounded-xl">
            <div className="flex justify-between items-end mb-2">
                <p className="text-sm text-slate-500 font-medium">Calculated Load</p>
                <p className="text-3xl font-bold text-blue-900">{result.flow.toFixed(1)} <span className="text-base font-normal text-slate-500">L/min</span></p>
            </div>
            
            <div className="h-px bg-blue-200 w-full my-3"></div>

            <div className="flex justify-between items-center">
                <p className="text-sm text-slate-500 font-medium">Recommended Pipe:</p>
                <p className="text-xl font-bold text-green-600">{result.pipe}</p>
            </div>
            
            <div className="mt-4 bg-white p-2 rounded text-center border border-slate-200">
                <p className="text-xs text-slate-400 uppercase tracking-wider font-bold">{result.note}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
